
import {Home, Blog, AboutMe, WrongRoute,Login, Name, Phone, AuthenticLogIn} from "../pages/index"

const publicRoutes = [
    {
        path: "/", 
        element : <Home/>
    },
    {
        path: "blog", 
        element : <Blog/>
    },
    {
        path: "aboutme", 
        element : <AboutMe/>, 
        route : [ 
            {
                path: "name",
                element: <Name/>
            },
            {
                path: "phone",
                element: <Phone/>
            }
        ]
    },
    {
        path: "login", 
        element : <Login/>
    },
    {
        path: "*", 
        element : <WrongRoute/>
    },{
        path: "user",
        element: <AuthenticLogIn/>,
        protect: true
    }

];
export {publicRoutes}