import axios from 'axios';
import "../style/LoginForm.css";
import LogInReducer from '../redux/reducer/LogInReducer';
import React from 'react'
import {  useDispatch, useSelector } from 'react-redux'
import LoadingReducer from '../redux/reducer/LoadingReducer';
import action from "../redux/action/actions"
import { type } from '@testing-library/user-event/dist/type';

const LoginForm = ()=>{
    const login = useSelector((state)=>{return state.LogInReducer})
    const handleLoading = useDispatch(LoadingReducer)
    
    const loginGG = (event)=>{
        event.preventDefault();
        axios.post("http://127.0.0.1:2000/login/auth/google")
    }
    
    const handleSubmit = (event)=>{
        event.preventDefault();
        const formElement = document.getElementById("form-login");
        const formData = new FormData(formElement);
        const UserAuthen = {
            username : formData.get("username"),
            password : formData.get("password")
        }
        handleLoading({type: action.LOADING})
        console.log(UserAuthen)
        axios.post("/login/auth",UserAuthen,{
            method: "POST",
            headers:{
                "Content-Type": "application/json"
            },
             auth : UserAuthen
            }).then((response)=>{
                console.log(response)
                if(response.status == 200){
                    handleLoading({type: action.LOGGED_IN})}
                else if (response.status==401){
                    handleLoading({type: action.NOT_LOGGED});
                }
            })
    }
    return (
        <>
        <form onSubmit={handleSubmit} id="form-login">
        <input className="form-element" name="username"  type="text" placeholder="Enter username"/>
        <input className="form-element" name="password" type="password" placeholder="Enter password"/>
        <button  className="form-element" type="submit"> Login</button>
        <a target="_top" href="http://localhost:2000/login/auth/google" className="form-element" type="button"> Login with Google</a>
        </form>
    </>
    )
}
export default LoginForm;