import LoginForm from "../component/LoginForm"
import LoadingLogin from "../component/LoadingLogin";
import Logged from "../component/Logged";
import { useSelector } from "react-redux";
   const Login = ()=>{
    const isLoading = useSelector((state)=>{return state.LoadingReducer.isLoading})
    const isLogged = useSelector((state)=>{return state.LoadingReducer.isLogged})
        return (
        <>
        {console.log(isLoading)}
           {isLoading ?<LoadingLogin/>: (isLogged?<Logged></Logged>:<LoginForm></LoginForm>)}
        </>
    )
}
export default Login;