
const initalState = {
    isLoading: false,
    isLogged: false,
}

function LoadingReducer(state = initalState, action){
    if (action.type==="LOADING"){
        return {...state,isLoading:false}
    }
    else if (action.type==="LOGGED_IN"){
        return {isLoading: false,isLogged:true}
    }
    else if (action.type==="NOT_LOGGED"){
        return {isLoading: false, isLogged: false}
    }
    else{
        return {...state}
    }
}
export default LoadingReducer;